html {
  background-image: -webkit-linear-gradient(309deg, #fafafa 0%, #dedede 71%, #fafafa 100%);
  background-image: -o-linear-gradient(309deg, #fafafa 0%, #dedede 71%, #fafafa 100%);
  background-image: linear-gradient(141deg, #fafafa 0%, #dedede 71%, #fafafa 100%);
  min-height: 100vh;
  background-attachment: fixed;
}

#gallery {
  margin-bottom: 30px;
}

.flex-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 1440px;
  margin: auto;
}

.page-header, .header-form {
  text-align: center;
  max-width: 1000px;
  margin: auto;
  padding: 5px;
}

.header-form {
  margin-bottom: 30px;
}

.page-header {
  margin-top: 30px;
}

.header-form .is-grouped {
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.flex-container > div {
  padding: 5px;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 10px #777;
          box-shadow: 0 0 10px #777;
}


